const iterator = ref(0);
const score = ref(0);
let randomisedList : QA[];

export default {
    init() {
        randomisedList = list
            .map(value => ({ value, sort: Math.random() }))
            .sort((a, b) => a.sort - b.sort)
            .map(({ value }) => value);
        iterator.value = 0;
        score.value = 0;
    },
    nextQuestion() {
        iterator.value++;
    },
    getQuestion() {
        return randomisedList[iterator.value];
    },
    hasMore() {
        if (iterator.value < randomisedList.length - 1) {
            return true;
        }
        return false;
    },
    pointUp() {
        score.value++;
    },
    score() {
        return score.value;
    }
}